import React, { useState } from 'react'
import '../styles/mainMenu.css'
import { Link } from 'gatsby'
import LangSelect from './LangSelect';

export default function MainMenu() {
    const [menuState, setMenuState] = useState('hide');
    const toggleMenu = () => {
        if (menuState === 'hide') setMenuState('show')
        else setMenuState('hide');
    }
    return (
        <div>
            <div className={menuState === 'hide' ? "side-menu-modal" : "side-menu-modal side-menu-modal-show"}>
                <div className={menuState === 'hide' ? "side-menu" : "side-menu side-menu-show"}>
                    <LangSelect />
                    <div className='side-menu-links'>
                        <Link activeClassName='active-link' to='/'>HOME</Link>
                        <Link activeClassName='active-link' to='/about'>ABOUT</Link>
                        <Link activeClassName='active-link' to='/news'>NEWS</Link>
                        <Link activeClassName='active-link' to='/analises'>ANÁLISES</Link>
                        <Link activeClassName='active-link' to='/videos'>VÍDEOS</Link>
                        <Link activeClassName='active-link' to='/forum'>FORUM</Link>
                    </div>
                </div>
            </div>
            <div className="main-menu-wrapper">
                <div className="container-xxl px-5 px-xxl-0">
                    <div className='main-menu'>
                        <div onClick={toggleMenu} className="toggle-button">
                            <div className={menuState === 'hide' ? "bar1" : 'bar1 bar1-toggle'}></div>
                            <div className={menuState === 'hide' ? "bar2" : 'bar2 bar2-toggle'}></div>
                            <div className={menuState === 'hide' ? "bar3" : 'bar3 bar3-toggle'}></div>
                        </div>
                        <Link to='/'><img src="/qkzclan/images/qkzclan-logo.svg" alt="" /></Link>
                        <LangSelect />
                        <div className="main-menu-links">
                            <Link activeClassName='active-link' to='/'>HOME</Link>
                            <Link activeClassName='active-link' to='/about'>ABOUT</Link>
                            <Link activeClassName='active-link' to='/news'>NEWS</Link>
                            <Link activeClassName='active-link' to='/analises'>ANÁLISES</Link>
                            <Link activeClassName='active-link' to='/videos'>VÍDEOS</Link>
                            <Link activeClassName='active-link' to='/forum'>FORUM</Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
